import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import { Button } from "react-bootstrap";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faShop } from "@fortawesome/free-solid-svg-icons";
import { NavLink, useNavigate } from "react-router-dom";
import { useContext, useState } from "react";
import DataContext from "../context/DataContext";
import { useEffect } from "react";

function NavbarComp() {
	//
  const [login, setLogin] = useState(true);
	const data = useContext(DataContext);
	// 
	const navigate = useNavigate();

	//     
	useEffect(()=>{
		setLogin(data.state.user ? true : false )
	}, [data.state.user]) //     

	// Logout   
	const logOut = () => {
		setLogin(false) //    
		// userd  null 
		data.action.setUser(null);
		//     
		navigate("/");
	}

  return (
    <>
      <Navbar bg="primary" variant="dark">
        <Container>
          <Navbar.Brand>
            <FontAwesomeIcon icon={faShop} /> SHOP
          </Navbar.Brand>
          <Nav className="me-auto">
            <NavLink to="/" className="nav-link">
              Home
            </NavLink>
          </Nav>
          <Navbar.Collapse className="justify-content-end">
            {login ? (
              <Nav >
                {/**    */}
                <NavLink className="nav-link" to="/mypage">
                  {data.state.user.name} MyPage
                </NavLink>
                <Button variant="outline-light" onClick={ logOut }>Logout</Button>{" "}
              </Nav>
            ) : (
              <div>
                {/**      */}
                <Button variant="outline-light" onClick={()=>{navigate('/loginform')}}>Login</Button>{" "}
              </div>
            )}
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </>
  );
}

export default NavbarComp;