// Context     
// Context , value    

import { useState } from "react";
import { createContext } from "react";
import { NavItem } from "react-bootstrap";
//  DataContext value  
const DataContext = createContext();

//  Provider  value   .
const DataProvider = ({children}) => {
    //   useState   
    //   {name: "", profile : , likelist : [] }
    const [user, setUser] = useState({name: "", profile : null, likelist : [] });
    
    //  :  
    const [productList, setProductList] = useState([
        {
            productId : 1,
            productName : "",
            productDetail : "  ",
            productColor : ["white", "black"],
            productPicture : ["1.png", "1-2.png" ]
        },
        {
            productId : 2,
            productName : "2",
            productDetail : "  ",
            productColor : ["white"],
            productPicture : ["1.png"]
        }
    ])
    // 
    const [allComments, setAllComments] = useState([
        {
            commentId : 1,
            productId : 1,
            name : "green",
            text : " "
        },
        {
            commentId : 2,
            productId : 1,
            name : "blue",
            text : ""
        }
    ])
    
    const [commentCount, setCommentCount] = useState(3);
    
    //  value  state action  
    const value = {
        state : {user, productList, allComments, commentCount},
        action : {setUser, setProductList, setAllComments, setCommentCount}
    };
    // DataProvider  DataContext.Provider  
    //  children Provider     
    return <DataContext.Provider value={value}>{children}</DataContext.Provider>
};

// consumer 
// DataContext   DataConsumer 
const { Consumer : DataConsumer } = DataContext;

//   export > .Provider    : App  
export { DataConsumer, DataProvider}
//     export >  useContext  
export default DataContext;